<template>
  <div class="database-content-box">
    <scroller style="top: 0;"
              :on-refresh="refresh"
              :on-infinite="infinite"
              ref="my_scroller"
              v-show="!isNoData"
    >
    <div class="my-line">
    </div>
    <ul :class="{'ul-databse-search':tieziFlag ===3,'ul-databse':tieziFlag !==3}">
      <li class="download-item" v-for="file in databases">
<!--        @click="goDataContent($event,file.abstract,file.credit,file.id,file.file_url,file.uploader,file.download_count)"-->
        <router-link  :to="'/datajifendl/'+file.id" class="download-item-link">
          <img v-lazy="file.file_image" alt="" class="download-item-pic">
          <div class="download-r">
            <p class="download-home-title">
              {{file.file_name}}
            </p>
            <p class="download-content">
             {{file.abstract}}
            </p>
            <div class="download-time-box">
                            <span class="download-time">
                                <span>{{file.uploaded_at}}</span>    磁易购    来自标签：<span v-for="(tag,index) in file.tags">{{tag.name}}<span v-show="(index+1)!==file.tags.length">、</span></span>
                            </span>
            </div>
          </div>
        </router-link>
      </li>
    </ul>
    </scroller>
    <NoData v-show="isNoData"></NoData>
  </div>
</template>

<script>
    import Api from '../../api/api';
    import NoData from '../nodata/NoData.vue';
    import logUtil from '../../utils/log';

    export default{
        props:{
          'tieziFlag':Number
        },
        components:{
          NoData
        },
        data(){
          return{
            //总页数
            totalCount:0,
            //第一页
            pageIndex:1,
            //每一页的数量
            pageSize:10,
            //资料集合
            databases:[],
            //新闻的标签
            newsTags:[],
            //是否有数据
            isNoData:false
          }
        },
        mounted(){
          this.$refs.my_scroller.finishInfinite(true);
          this.getDataBaseDatas(0,1);
        },
        //自定义过滤器
       /* filters:{
            /!**
             * time filter:just get the format of year-month-day
             * *!/
            formatTime(timeStr){
              const strArry = timeStr.split('T');
              return strArry[0];
            }
        },*/
        methods:{
            /**
             * 从服务器端获取资料库的数据
             * */
            getDataBaseDatas(type,page){
              const that = this;
              this.$http.get(Api.FILES,{
                params:{
                  type:0,
                  page:page,
                  page_size:that.pageSize,
                }
              }).then((res)=>{
                logUtil.printLog("获取到的资料库的信息是:",res);
                if(res.status === 200){
                  that.initData(type,res);
                }else{
                  that.isNoData = true;
                }
              },(err)=>{
                that.isNoData = true;
                that.$store.dispatch('hideLoading');
                that.showMsg('获取数据失败，请重试');
              });
            },
            /**
             *初始化资料库的数据
             * */
            initData(type,res){
              const that = this;
              that.totalCount = res.data.data.total;
              if(res.data!==null && res.data.data.detail.length>0){
                that.isNoData = false;
                let tempArr = [];
                tempArr = res.data.data.detail;
                if(type===0){
                  that.databases = tempArr;
                }
                //下拉刷新
                else if(type===1){
                  that.databases=tempArr.splice(0,that.pageSize);
                }else if(type === 2){ //上拉加载
                  that.databases = that.databases.concat(tempArr);
                }
                let newsLen = that.databases.length;
                for(let i = 0; i<newsLen;i++){
                  that.newsTags = that.databases[i].tags;
                }
              }else{
                that.isNoData = true;
                that.showMsg('当前暂无数据!');
              }

            },
            /**
             * 处理错误信息
             * */
            dealErr(){
              this.showMsg('获取数据失败，请重试!');
              this.$store.dispatch('hideLoading');
            },
            showMsg(str){
              this.$toast({
                message: str,
                duration: 2000,
              });
            },
            goDataContent($event,abstract,credit,id,url,uploader,downloadCount){
                this.$router.push({path: `/datajifendl/${id}/${abstract}/${credit}/${uploader}/${downloadCount}`, replace: true});
                localStorage.setItem('url',url);
                $event.preventDefault();
            },
          /**
           * 刷新
           */
          refresh(done){
            const that = this;
            that.pageIndex = 1;
            that.type = 1;
            setTimeout(() => {
              that.getDataBaseDatas(that.type,that.pageIndex);
              done()
            }, 1500)
          },
          //加载更多
          infinite(done){
            const that = this;
            that.pageIndex++;
            that.type = 2;
            setTimeout(() => {
              //下一页数如果大于总页数，说明就没有跟多数据了
              if(that.pageIndex>=that.totalCount){
                done(true);
                return;
              }
              done();
              that.getDataBaseDatas(that.type,that.pageIndex);
            }, 1500)
          }
        }
    }
</script>
<style scoped>
    @import "../../assets/css/database.css";
</style>



